@use 'font';
@use 'typeset';


:root {
	@include font.custom-property();
	@include typeset.custom-property();
}

@for $i from 1 through 16 {
	[data-nc-font='#{$i}'] {
		@include typeset.font-size-line-height($i);
	}
}
